.showcase {
  width: 320px;
  margin: 0 auto;

  font-family: "opensansbold", "Arial", sans-serif;
}

.showcase-header {
  background-image: url("../img/hero-mobile@1x.jpg");
  background-position-x: center;
  background-position-y: -20px;

  &__slogan {
    margin: 0;
    padding-top: 71px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 119px;

    font-size: 27px;
    line-height: 30px;
    font-weight: bold;
    text-align: center;
  }
}

.showcase-list {
  padding: 0;
  margin: 0;

  font-size: 19px;
  line-height: 24px;
  font-weight: bold;
  text-align: left;


  &__item {
    position: relative;

    background-color: #64d1bc;
    border: none;

    list-style: none;

    &--interior::after {
      content: "";

      position: absolute;
      top: 23px;
      right: 36px;

      width: 40px;
      height: 62px;

      background-image: url("../img/icon-interior.svg");
      opacity: 0.5;
    }

    &--text {
      display: inline-block;

      padding-top: 28px;
      padding-bottom: 28px;
      padding-right: 160px;
      padding-left: 28px;

      color: #ffffff;
    }

    &--toy::after {
      content: "";

      position: absolute;
      top: 23px;
      right: 36px;

      width: 60px;
      height: 60px;

      background-image: url("../img/icon-toy.svg");
      opacity: 0.5;
    }
  }

  &__item:nth-child(2n) &__item--text {
    background-color: #57c3b3;
  }
}
